@import './common.sass';

body {
  min-height: 100vh;
  background-color: $white;
  display: flex;
  flex-direction: column; }

#root {
  display: flex;
  flex-grow: 1; }


// HEADER [HEAD]

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $red;
  width: 100%;
  height: 60px;
  color: $white;
  font-family: $brand-font;
  user-select: none;
  a {
    text-decoration: none;
    color: inherit; } }


.header__brand {
  display: flex;
  font-size: 2.5em;
  padding-left: 20px; }

.header__brand__logo {
  min-height: 30px;
  min-width: 40px;
  display: flex;
  margin: 3px;
  justify-content: space-between;
  align-items: flex-end; }

.header__brand__logo__bar {
  min-height: 40px;
  min-width: 10px;
  background-color: $white;
  border-radius: 1.5px; }

.header__brand__logo__bar1 {
  min-height: 40%; }

.header__brand__logo__bar2 {
  min-height: 70%; }

.header__brand__logo__bar3 {
  min-height: 100%; }

.header__brand__sorted {
  padding-left: 20px; }

// NAV [NAV]

.header__nav {
  display: flex;
  justify-content: space-between;
  font-size: 1.3em; }

.header__nav__item {
  margin: 20px; }
.header__nav__item > a {
  position: relative; }
.header__nav__item > a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: $white;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.15s ease-in-out 0s; }
.header__nav__item > a:hover:before {
  visibility: visible;
  transform: scaleX(1); }



// FOOTER [FOOT]

.footer {
  color: $black;
  width: 100%;
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  text-align: center;
  padding: 10px;
  a {
    text-decoration: none;
    color: inherit; } }

